<template>
  <Layout id="map">
    <TalIframe :content-src="dataSrc" />
  </Layout>
</template>

<script>
import TalIframe from '@/components/iframe'
export default {
  name: 'Map',
  data () {
    return {
      dataSrc: ''
    }
  },
  components: { TalIframe },
  mounted () {
    this.dataSrc = '/tools/pcmap/?from=service&platform=splat'
    if (process.env.NODE_ENV === 'development') {
      this.dataSrc = ' http://localhost:8080/tools/pcmap?from=service'
    }
  }
}
</script>
